<template>
    <v-container class="bg-white">
        <v-banner single-line class="h5">
          <img width="32" height="32" src="https://bizlx.itechvision.in/images/accounts.png">
          All {{alljobs.jobcatid.job_cat_name}} Jobs
        </v-banner>
          <v-row class="py-0 py-md-5">
            <v-col cols="6" md="2" v-for="(job,i) in alljobs.jobs" :key="i">
              <v-card v-if="jobs.length > 0">
                <router-link :to="{ name: 'jobsbycatcity', params: {city: job.city}}">
                  <v-card-text class="text-center h6">
                    <span class="me-1">{{job.city}}</span>
                    <span class="text--white badge bg-dark">{{job.jobcount}}</span>
                  </v-card-text>
                </router-link>
              </v-card>
            </v-col>
          </v-row>
    </v-container>
  </template>
  
  <script>
  import axios from "axios";

  export default {
    name: "AllCatjobs",
    props: {
         id: Number,
         job_cat_slug: String
    },
    data (){
      return{
        jobs:[
          {job_count: '5', city: 'Agra'},
          {job_count: '6', city: 'Delhi'},
          {job_count: '10', city: 'Bengluru'},
          {job_count: '23', city: 'Chamba'},
          {job_count: '49', city: 'Chandigarh'},
          {job_count: '53', city: 'Jammu'},
          {job_count: '34', city: 'Mohali'},
          {job_count: '21', city: 'Ambala'},
          {job_count: '29', city: 'Jalandhar'},
          {job_count: '26', city: 'Ludhiana'},
          {job_count: '31', city: 'Shimla'},
          {job_count: '43', city: 'Rajpura'}
        ],
        alljobs: '',
      }
    },
    mounted() {
      this.getJobcategory();
    },
    methods:{
      getJobcategory() {
         axios.get(`jobcategory/${this.job_cat_slug}`)
            .then((resp) =>{
              this.alljobs = resp.data;
            });
      },
    }
  }
  </script>
  
  <style scoped>
  .v-banner__text {
    display: flex;
}
  </style>